.gpt3__features-container__feature {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-direction: row;
    margin: 1rem;
}

.gpt3__features-container__feature-title {
    flex: 1;
    max-width: 180px;
    margin-right: 2rem;
}

.gpt3__features-container__feature-title h1 {
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 18px;
    line-height: 24px;
    letter-spacing: -0.04em;
    color: #FFFFFF;

}

.gpt3__features-container__feature-title div {
    width: 38px;
    height: 3px;
    background: var(--graident-bar);
    box-shadow: 0 4px 4px rgba(0, 0, 0, 0.5);
    margin-bottom: 0.23rem;
}

.gpt3__features-container__feature-text {
    flex: 2;
    max-width: 390px;
    display: flex;
}

.gpt3__features-container__feature-text p {
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 14px;
    line-height: 24px;
    color: var(--color-text);
}
